import { useReducer } from 'react';

export const ADD_TO_CART_ACTION_TYPES = {
  FAIL: 'FAIL',
  FETCHING: 'FETCHING',
  SUCCESS: 'SUCCESS',
};

export const defaultState = {
  actionTypes: {},
  dispatch: () => {},
  onClick: () => {},
  state: {
    errorMessage: '',
    hasError: false,
    isLoading: false,
    isUpdateSuccessful: false,
  },
};

function reducer(state, action) {
  if (action.type === ADD_TO_CART_ACTION_TYPES.FETCHING) {
    return {
      errorMessage: '',
      hasError: false,
      isLoading: true,
      isUpdateSuccessful: false,
    };
  } else if (action.type === ADD_TO_CART_ACTION_TYPES.SUCCESS) {
    return {
      errorMessage: '',
      hasError: false,
      isLoading: false,
      isUpdateSuccessful: true,
    };
  } else if (action.type === ADD_TO_CART_ACTION_TYPES.FAIL) {
    return {
      errorMessage: action.payload,
      hasError: true,
      isLoading: false,
      isUpdateSuccessful: false,
    };
  }

  throw new Error(
    `useAddToCartStore: Dispatch action type "${action.type}" not found.`,
  );
}

export const useAddToCartStore = onClick => {
  const [state, dispatch] = useReducer(reducer, defaultState);

  return {
    actionTypes: ADD_TO_CART_ACTION_TYPES,
    dispatch,
    onClick,
    state,
  };
};
